@import "../library/textInput/textInput";
@import "../library/dropdown/dropdown";
@import "../library/date/date";
@import "../library/dateFilter/dateFilter";
@import "../library/subscrTextField/subscrTextField";
@import "../library/checkboxList/checkBoxList";









.checkbox-btns {
    width: 266px;
}

.checkbox-btns--description {
    text-transform: uppercase;
    @include MontserratBold();
    margin-bottom: 30px;
}

.checkbox-btns--list-item {
    display: flex;
}

.checkbox-btns-item-chek {
    width: 20px;
    height: 20px;
    background: #fff;
    @include flexAllCenter();
    @include inputBorder();
    margin-right: 10px;
    transition: all .1s linear;
    cursor: pointer;
}

.checkbox-btns--list-item {
    margin-bottom: 10px;
}

.checkbox-btns-item-description {
    width: 192px;
    @include Montserrat();
    color: $darkShade50;
}

.checkbox-btns-item-chek-active {
    border-color: $purple;
}

.checkbox-btns-item-chek--checked {
    opacity: 0;
    transition: all .1s linear;
}

.checkbox-btns-item-chek--checked-active {
    opacity: 1;
}

.radio-btns {
    width: 220px;
}

.radio-btns--description {
    text-transform: uppercase;
    @include MontserratBold();
    margin-bottom: 30px;
}
.radio-btns--list{
    display: flex;
    justify-content: space-around;
}
.radio-btns--list-item {
    @include flexVertCenter();
    margin-bottom: 10px;
}

.radio-btns-item-chek {
    width: 20px;
    height: 20px;
    @include inputBorder();
    border-radius: 50%;
    margin-right: 10px;
    @include flexAllCenter();
    cursor: pointer;
}

.radio-btns-item-chek-active {
    border-color: $purple;
}

.radio-btns-item-chek--checked {
    opacity: 0;
    transition: all .1s linear;
}

.radio-btns-item-chek--checked-active {
    opacity: 1;
}
.toggle-switch{
    width: 260px;
}
.toggle-switch-wrapper{
    display:flex; 
    justify-content: space-around;
}
.toggle-switch--switchWrapper{
    width:40px;
    height: 20px;
    border: 1px solid $darkShade25;
    border-radius:10px;
    position:relative;
    cursor:pointer;
    margin-right:10px;
}
.toggle-switch--switchWrapper-active{
    border-color:$purple;
}
.switchWrapper-switch{
    width:12px;
    height:12px;
    border-radius:50%;
    background: rgba(31, 32, 65, 0.25);
    position:absolute;
    top:3px;
    left:3px;
    transition:all .1s linear;
}
.switchWrapper-switch-active{
    background: linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
    left:23px;
}
.toggle-switch--description{
    @include Montserrat();
}
.likeBtnBorder{
    width:40px;
    height:20px;
    background:$darkShade25;
    @include flexAllCenter();
    border-radius:10px;    
    transition:all .1s linear;
}
.likeBtnBorder-active{
    background: linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
}
.likeBtn-wrapper{
    width:38px;
    height:18px;
    background:#fff;
    border-radius:10px;
    @include flexVertCenter();
    transition:all .1s linear; 
    cursor:pointer;
}

.likeBtn--heart{
    width:20px;
    height:20px;
    padding-left:5px;
}
.likeBtn--heart-icon{
    font-size:10px;
    line-height: 20px;
    color:$darkShade25;
    transition:all .1s linear;
}
.likeBtn--heart-icon-active{
    color:$purple;
}
.likeBtn--num{
    font-family:Montserrat;
    font-size:10px;
    color:$darkShade25;
    transition:all .1s linear;
}
.likeBtn--num-active{
    color:$purple;
}
.rateBtns{
    width:120px;
    @include flexVertCenter();
}
.rateBtns-line {
    @include flexVertCenter();
}
.rateBtns--star{
    margin:0px 2px;
    cursor:pointer;
    @include flexAllCenter();
}
.rateBtns--star-icon{
    @include flexVertCenter();
    background:linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    transition:all .1s linear;
}
.rateBtns--star-icon-active{
    transition: all .1s linear;
}

.richCheckBox{
    width:266px;
}
.richCheckBox--description{
    text-transform: uppercase;
    @include MontserratBold();
    margin-bottom:10px;
}
.richCheckBox--items-item{
    display: flex;
    justify-content: space-between;
    margin-bottom:10px;

}
.richCheckBox--items-item-chek{
    width:20px;
    height:20px;
    @include inputBorder();
    margin-right: 10px;
    transition: all .1s linear;
    cursor: pointer;
    @include flexAllCenter();
}
.richCheckBox--items-item-chek-active{
    border-color:$purple;
}
.richCheckBox--items-item-chek--checked{
    transition:all .1s linear;
    opacity: 0;
}
.richCheckBox--items-item-chek--checked-active{
    opacity: 1;
}
.richCheckBox--items-item-description{
    width:236px;
}
.richCheckBox--items-item-description-heading{
    @include MontserratBold();
    font-size:14px;
    color:$darkShade75;
    margin-bottom:5px;
}
.richCheckBox--items-item-description-text{
    @include Montserrat();
    font-size:12px;
    color:$darkShade50;
}
.bulletList{
    width:292px;
}
.bulletList--description{
    text-transform: uppercase;
    @include MontserratBold();
    margin-bottom:10px;
}
.bulletList--list-item{
    display:flex;
    align-items: baseline;
    margin-bottom:10px;
}
.bulletList--list-item-marker{
    width:10px;
    height:10px;
    border-radius:50%;
    background: $darkShade25;
    margin-right:10px;
}
.bulletList--list-item-description{
    width:240px;
    @include Montserrat();
    color:$darkShade75;
}
.filledBtn--button{
    height:44px;
    padding:14px 20px;
    text-transform: uppercase;
    @include MontserratBold();
    border:none;
    border-radius:22px;
    background: linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
    color:#fff;
    cursor: pointer;
    transition:all .1s linear;
        &:focus{
            outline: none;
        }
        &:active{
            opacity: 0.5;
        }
}
.borderedBtn-wrapper{
    height:44px;
    width:105px;
    border-radius:22px;
    background: linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
    cursor: pointer;
    transition:all .1s linear;
    @include flexAllCenter();
        &:active{
            opacity: 0.5;
        }
}
.borderedBtn--button{
    height:40px;
    padding:14px 20px;
    text-transform: uppercase;
    @include MontserratBold();
    border:none;
    border-radius:22px;
    background: #fff;
    color:$purple;
    cursor: pointer;
    transition:all .1s linear;
        &:focus{
            outline: none;
        }
        &:active{
            opacity: 1;
        }
}
.textBtn--button{
    text-decoration: none;
    @include MontserratBold();
    text-transform: uppercase;
    color:$darkShade50;
    transition: all .1s linear;
    &:hover, &:active{
        color:$purple;
    }
}
.longBtn--button{
    position:relative;
    width:320px;
    height:44px;
    text-transform: uppercase;
    @include MontserratBold();
    border:none;
    border-radius:22px;
    background: linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
    color:#fff;
    cursor: pointer;
    transition:all .1s linear;
        &:focus{
            outline: none;
        }
}
.longBtn--arrow-icon{
    position: absolute;
    top:10px;
    right:15px;
}
.pagination{
    width:240px;
}
.pagination--line{
    display:flex;
    justify-content: space-between;
    font-family: 'Montserrat';
    font-size:12px;
    margin-bottom:10px;
}
.pagination--line-elem{
    width:40px;
    height:40px;
    @include flexAllCenter();
    color:$darkShade50;
    background: #fff;
    border-radius:50%;
    cursor:pointer;
    transition: all .1s linear;
}
.pagination--line-elem-active{
    color:#fff;
    background: linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
}
.pagination--options{
    font-family: 'Montserrat';
    font-size:14px;
    @include flexAllCenter();
}
.pagination--line-elem-arrow-icon{
    cursor:pointer;
    color:#fff;
    background: linear-gradient(180deg, $green 0%, #66D2EA 100%);
}
.range{
    width:266px;
}
.range--description{
    display:flex;
    justify-content: space-between;
    margin-bottom:10px;
}
.range--description-text{
    @include MontserratBold();
    text-transform: uppercase;
}
.range--description-values{
    display:flex;
    font-family: 'Montserrat';
    font-size:12px;
}
.slider--base{
    position:relative;
    width:100%;
    height:6px;
    @include inputBorder();
    border-radius:3px;
    display:flex;
    align-items:center;
    justify-content: center;
    z-index: 0;
}
.slider--base-hover{
    position:absolute;
    z-index: 0;
    width: 37%;
    height:6px;
    background: linear-gradient(180deg, $green 0%, #66D2EA 100%);
    border-radius:3px;
    cursor:pointer;
}
.slider--left-btn, .slider--right-btn{
    position: absolute;
    z-index: 2;
    width:14px;
    height:14px;
    border:2px solid #fff;
    background: linear-gradient(180deg, $green 0%, #66D2EA 100%);
    border-radius: 50%;
    cursor:pointer;
}
.slider--left-btn{
    left:80px;
    cursor:pointer;
}
.slider--right-btn{
    right:80px;
}
.comment{
    width:710px;
}
.comment-wrapper{
    display:flex;
    justify-content: space-between;
}
.comment-author{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    margin-right:10px;
    margin-bottom:10px;
}
.comment-content{
    display:flex;
    flex-direction: column;
    @include Montserrat();
    color:$darkShade75;
}
.author-name{
    font-family: 'Montserrat-Bold';
    font-size:14px;
    color:$darkShade75;
    margin-bottom:10px;
}
.author-comment-date{
    margin-bottom:5px;
}
.comment-text{
    line-height: 24px;
}
.advantages{
    width:290px;
}
.advantages-item{
    display:flex;
    align-items: center;
    margin-bottom:10px;
}
.advantages-item-image-icon{
    font-size:48px;
    background:linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    transition:all .1s linear;
    margin-right:10px;
}
.description-heading{
    font-family: 'Montserrat-Bold';
    font-size:14px;
    margin-bottom:5px;
}
.description-text{
    @include Montserrat();
}
.advantages-item-line{
    width:280px;
    background: rgba(31, 32, 65, 0.1);
    border:none;
    height:1px;
    margin-bottom:10px;
}
.colors{
    width:270px;
}
.colors-item{
    @include flexVertCenter();
    margin-bottom:20px;
}
.colors-block{
    width:70px;
    height:70px;
    border-radius: 6px;
    margin-right:30px;
}
.colors-heading{
    margin-bottom:10px;
}
.type{
    width: 403px;
}
.type-item{
    display:flex;
    justify-content: space-between;
    margin-bottom:50px;
}
.type-tag{
    width:40px;
    text-align: right;
}
.type-description{
    width:336px;
}
.calendar{
    width: 320px;
    border: 1px solid rgba(31, 32, 65, 0.25);
    border-radius: 4px;
}
.calendar-wrapper{
    width:100%;
    max-width: 280px;
    margin: 20px auto;
}
.calendar-heading{
    display:flex;
    justify-content:space-between;
    align-items:center;
}
.calendar-month{
    margin-right:8px;
}

.calendar-heading-arrow{
    @include flexVertCenter();
    color:$purple;
    cursor: pointer;
}
.calendar-table{
    width:100%;
    margin-top:30px;
    border-spacing: 0px;
}
.calendar-table-head th{
    @include MontserratBold();
    color:$purple;
    width:40px;
    height:40px;
}
.calendar-table-body{
    width:100%;
    font-size:12px;
}
.calendar-table-body td{
    width:40px;
    height:40px;
    text-align: center;
    cursor: pointer;
    position:relative;
}
.calendar-last-month, .calendar-next-month{
    color:$darkShade25;
}
.calendar-buttons{
    display: flex;
    justify-content: space-between;
    @include MontserratBold();
    text-transform: uppercase;
    cursor: pointer;
}
.calendar-confirm-link{
    color:$purple;
}
.today-active{
    font-weight: bold;
    color:#fff;
    background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
    border-radius:50%;
}
.calendar-month-cell-active{
    font-weight: bold;
    color:#fff;
    background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
    border-radius:50%;
}
.calendar-month-cell-active-start{
    position:relative;
    z-index: 1;
    &:after{
        content: '';
        display:block;
        position:absolute;
        top:0;
        right:0;
        z-index:-1;
        width:20px;
        height:40px;
        background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
        opacity:0.25;
    }
}
.calendar-month-cell-active-end{
    position:relative;
    z-index: 1;
    &:after{
        content: '';
        display:block;
        position:absolute;
        top:0;
        left:0;
        z-index:-1;
        width:20px;
        height:40px;
        background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
        opacity:0.25;
    }
}
.calendar-month-cell-active-range{
    z-index: 1;
    &:after{
        content: '';
        width:40px;
        height:40px;
        position: absolute;
        top:0;
        left:0;
        z-index: -1;
        background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
        opacity:0.25;
    }
}